.congrad {
    .decor {
        display: none; }
    footer.footer {
        position: relative;
        overflow: hidden;
        z-index: 2; }
    header.header {
        .wrapper-phone {
            a {
                color: #274386; } }
        .wrapper-phone-desc {
            p {
                color: #000; } } } }
main.main-congrad {
    margin-top: 81px;
    @include r($tablet) {
        margin-top: 50px; }
    .container {
        padding-bottom: 200px;
        position: relative;
        @include r($desktop) {
            padding-bottom: 100px; }
        @include r($tablet) {
            padding-bottom: 40px; } }
    .inner-container {
        position: relative;
        z-index: 2; }
    h1 {
        font-size: 40px;
        color: #274386;
        line-height: 50px;
        text-align: center;
        font-weight: 700;
        letter-spacing: 0.2px;
        margin-bottom: 45px;
        @include r($tablet) {
            font-size: 30px;
            line-height: normal; }
        span {
            text-transform: uppercase;
            color: #d12835;
            display: block; } }
    .wrapper-video {
        margin-right: auto;
        margin-left: auto;
        width: 774px;
        height: 422px;
        border: 2px solid #d12835;
        border-radius: 10px;
        overflow: hidden;
        margin-bottom: 60px;
        @include r($desktop) {
            width: 600px; }
        @include r($tablet) {
            width: 100%;
            height: 300px; }
        @include r($mobile) {
            height: 200px; }
        iframe {
            width: 100%;
            height: 100%; } }
    .next-webinar {
        border-radius: 10px;
        border: 2px dashed #cd1523;
        padding: 38px 160px;
        text-align: center;
        margin-bottom: 70px;
        @include r($wide) {
            padding: 38px 42px; }
        @include r($tablet) {
            padding: 38px 10px; }
        h2 {
            color: #274386;
            margin-bottom: 25px;
            @include r($desktop) {
                margin-bottom: 10px;
                line-height: normal; } }
        h2, p.time {
            text-align: center;
            font-size: 30px;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: 0.15px;
            @include r($tablet) {
                font-size: 20px; } }
        p.time {
            color: #ce1826; } }
    .wrapper-tutorial {
        padding: 0 100px;
        @include r($tablet) {
            padding: 0 50px; }
        @include r($mobile) {
            padding: 0 5px; }
        ol {
            list-style: none;
            counter-reset: li; }
        li {
            position: relative;
            box-shadow: 0 0 25px rgba(51, 65, 78, 0.16);
            border-radius: 10px;
            padding: 30px 27px 30px 42px;
            background-color: #ffffff;
            margin-bottom: 80px;
            @include r($tablet) {
                padding-right: 10px; }
            @include r($mobile) {
                padding-left: 5px;
                padding-right: 5px; }
            &::before {
                content: counter(li);
                counter-increment: li;
                display: block;
                position: absolute;
                z-index: 2;
                top: -50px;
                left: 0px;
                color: #d9deea;
                font-size: 80px;
                font-weight: 700; }
            p {
                color: #353535;
                font-size: 16px;
                font-weight: 400;
                line-height: 22.04px;
                letter-spacing: 0.24px; }
            .btn {
                position: relative;
                bottom: 0;
                left: 0;
                transform: translateY(50%);
                display: block;
                text-decoration: none;
                max-width: 370px;
                width: 100%;
                text-align: center;
                padding: 16px 0 16px 0;
                @include r($mobile) {
                    font-size: 14px; } } }
        .has-btn {
            padding-bottom: 0; } }
    .wrapper-icon-decor {
        position: absolute;
        svg {
            fill: #f4f7fd; } }
    .wrapper-icon-decor:nth-child(1) {
        top: 43px;
        left: -33px;
        svg {
            width: 319px;
            height: 318px;
            transform: rotate(-17deg); } }
    .wrapper-icon-decor:nth-child(2) {
        top: 113px;
        right: -631px;
        svg {
            width: 600px;
            height: 599px;
            transform: rotate(-22deg); } }
    .wrapper-icon-decor:nth-child(3) {
        top: 330px;
        left: -310px;
        svg {
            width: 215px;
            height: 215px;
            transform: rotate(-22deg); } }
    .wrapper-icon-decor:nth-child(4) {
        bottom: -200px;
        left: -449px;
        svg {
            width: 539px;
            height: 538px;
            transform: rotate(-42deg); } } }
