// FOOTER
footer.footer {
    padding: 30px 0;
    background-color: #0f224e;
    @include r($mobile) {
        padding: 20px 0; }
    .container {
        display: flex;
        justify-content: space-between;
        @include r($tablet) {
            flex-direction: column;
            align-items: center; } }
    .wrapper-logo {
        max-width: 388px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include r($tablet) {
            margin-bottom: 10px; }
        @include r($mobile) {
            flex-direction: column; } }
    .wrapper-img {
        max-width: 213px;
        width: 100%;
        margin-right: 18px;
        @include r($tablet) {
            margin-right: 0; } }
    img {
        width: 100%; }
    h2 {
        color: #fff;
        text-transform: none;
        font-family: 'Roboto';
        font-size: 16px;
        font-weight: 500;
        line-height: 20px;
        letter-spacing: 0.24px;
        @include r($tablet) {
            max-width: 160px;
            width: 100%; }
        @include r($mobile) {
            max-width: 154px; } }
    .wrapper-txt {
        max-width: 418px;
        width: 100%;
        p {
            margin-top: 9px;
            color: rgba(#ffffff,0.8);
            font-size: 13px;
            font-weight: 300;
            line-height: 17px;
            letter-spacing: 0.48px;
            text-align: right;
            @include r($tablet) {
                text-align: center; } } }
    .wrapper-links {
        text-align: right;
        @include r($tablet) {
            text-align: center; }
        a {
            color: #ffffff;
            font-size: 14px;
            font-weight: 400;
            line-height: 30.04px;
            text-decoration: underline;
            letter-spacing: 0.56px;
            @include r($mobile) {
                display: block; }
            &::after {
                content: '|';
                margin-left: 5px;
                margin-right: 5px;
                @include r($mobile) {
                    display: none; } } }
        a:last-child {
            &::after {
                display: none; } } }
    .copy {
        margin-top: -14px;
        color: #ffffff;
        font-size: 14px;
        font-weight: 300;
        font-size: 12px;
        letter-spacing: 0.48px;
        @include r($tablet) {
            margin-top: 0; } } }

