// Styles for dialog window
.mfp-wrap {
  -webkit-overflow-scrolling: touch;
  -webkit-transform: translateZ(0); }

html.mfp-helper {
  height: 100%;
  body {
    overflow: hidden;
    height: 100%;
    -webkit-transform: translateZ(0); } }

#small-dialog {
  background: white;
  padding: 20px 30px;
  text-align: left;
  max-width: 600px;
  margin: 40px auto;
  position: relative; }
.mfp-close-btn-in .mfp-close {
  color: #000;
  right: -5px;
  top: -5px; }
.mfp-image-holder .mfp-close,
.mfp-iframe-holder .mfp-close {
  width: inherit;
  min-width: 33px; }
.white-popup-block {
  max-width: 451px;
  width: 100%;
  padding: 40px;
  margin: auto;
  position: relative;
  background-color: #ffffff;
  .input-text {
    border: 1px solid #fff; }
  input[type='tel']:invalid {
    border: 1px solid #c90312; }
  .ui-group:last-child {
    margin-bottom: 0; } }

.popup-buble {
  .popup-dialog {
    opacity: 0;
    transition: all 0.2s ease-in-out;
    transform: scale(0.8); }
  &.mfp-ready .popup-dialog {
    opacity: 1;
    transform: scale(1); }
  &.mfp-removing .popup-dialog {
    transform: scale(0.8);
    opacity: 0; }
  &.mfp-bg {
    background: rgba(0, 0, 0, 0.6);
    opacity: 0;
    transition: opacity 0.3s $ease; }
  &.mfp-ready.mfp-bg {
    opacity: 1; }
  &.mfp-removing.mfp-bg {
    opacity: 0; } }
.modal-form-tnx {
  .popup-dialog {} }
