/* HEADER */
.page {
    overflow: hidden; }
header.header {
    min-height: 80px;
    background-color: #f6f6f6;
    padding: 12px 0;
    position: relative;
    .decor {
        position: absolute;
        right: -579px;
        top: -421px;
        width: 939px;
        height: 942px;
        z-index: 1;
        @include r($tablet) {
            display: none; } }
    .container {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include r($tablet) {
            flex-direction: column; } }
    .wrapper-logo {
        max-width: 388px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include r($tablet) {
            margin-bottom: 10px; }
        @include r($mobile) {
            flex-direction: column; } }
    .wrapper-img {
        max-width: 213px;
        width: 100%;
        margin-right: 18px;
        @include r($mobile) {
            margin-right: 0; } }
    img {
        width: 100%; }
    h2 {
        color: #000000;
        text-transform: none;
        font-family: 'Roboto';
        font-size: 16px;
        font-weight: 500;
        line-height: 20px;
        letter-spacing: 0.24px;
        @include r($tablet) {
            max-width: 160px;
            width: 100%; }
        @include r($mobile) {
            max-width: 154px; } }
    .wrapper-phone-desc {
        position: relative;
        z-index: 2; }
    a[href^="tel:"] {
        color: #ffffff;
        font-size: 24px;
        font-weight: 700;
        letter-spacing: 0.36px;
        text-decoration: none;
        @include r($tablet) {
            color: #000; } }
    svg {
        width: 18px;
        height: 18px;
        fill: #ed3b4b;
        margin-right: 10px; }
    .wrapper-phone {
        margin-bottom: 1px; }
    p {
        color: rgba(#ffffff,0.6);
        font-size: 14px;
        font-weight: 400;
        letter-spacing: 0.21px;
        text-align: right;
        margin-right: 2px;
        @include r($tablet) {
            color: rgba(0,0,0,0.6); } } }

