.final-step {
    .decor {
        display: none; }
    footer.footer {
        position: relative;
        overflow: hidden;
        z-index: 2; }
    header.header {
        .wrapper-phone {
            a {
                color: #274386; } }
        .wrapper-phone-desc {
            p {
                color: #000; } } } }
main.main-final-step {
    margin-top: 70px;
    .container {
        padding-bottom: 120px;
        position: relative;
        @include r($tablet) {
            padding-bottom: 70px; }
        @include r($mobile) {
            padding-bottom: 30px;
            padding-left: 10px;
            padding-right: 10px; } }
    .inner-container {
        position: relative;
        z-index: 2; }
    .wrapper-icon-decor {
        position: absolute;
        svg {
            fill: #f4f7fd; } }
    .wrapper-icon-decor:nth-child(1) {
        bottom: -139px;
        left: -492px;
        svg {
            width: 539px;
            height: 538px;
            transform: rotate(53deg); } }
    .wrapper-icon-decor:nth-child(2) {
        top: 0px;
        right: -532px;
        svg {
            width: 608px;
            height: 549px; } }
    h1 {
        text-align: center;
        color: #d12835;
        font-size: 40px;
        font-weight: 700;
        letter-spacing: 0.2px;
        margin-bottom: 5px;
        @include r($tablet) {
            font-size: 35px; } }
    h1 + p {
        color: #353535;
        font-size: 20px;
        font-weight: 500;
        letter-spacing: 0.1px;
        text-align: center;
        @include r($mobile) {
            font-size: 18px; } }
    .wrapper-buttons {
        margin-top: 25px;
        max-width: 377px;
        width: 100%;
        margin-right: auto;
        margin-left: auto;
        margin-bottom: 70px;
        @include r($mobile) {
            margin-bottom: 40px; }
        a {
            width: 100%;
            padding: 16px 0;
            display: block;
            text-align: center;
            color: #ffffff;
            font-size: 16px;
            font-weight: 700;
            letter-spacing: 0.24px;
            border-radius: 5px;
            text-decoration: none;
            margin-bottom: 18px;
            display: flex;
            justify-content: center;
            align-items: center;
            &::before {
                margin-right: 7px; } }
        a:last-child {
            margin-bottom: 0; }
        a:nth-child(1) {
            background-image: linear-gradient(to top, #2c7ec9 0%, #5fa8e9 100%);
            &::before {
                content: url(../img/ic-message-white.png); } }
        a:nth-child(2) {
            background-image: linear-gradient(to top, #f3d326 0%, #ffe143 100%);
            color: #000000;
            &::before {
                content: url(../img/ic-message-black.png); } }
        a:nth-child(3) {
            background-image: linear-gradient(to top, #e25440 0%, #e56c5d 100%);
            &::before {
                content: url(../img/ic-message-white.png); } } }
    .tutorial {
        max-width: 780px;
        width: 100%;
        margin-right: auto;
        margin-left: auto;
        color: #274386;
        font-size: 16px;
        font-weight: 700;
        line-height: 22px;
        letter-spacing: 0.04px;
        padding: 33px;
        text-align: center;
        border-radius: 10px;
        border: 2px dashed #cd1523;
        margin-bottom: 79px;
        @include r($tablet) {
            padding: 10px;
            margin-bottom: 40px; } }
    .wrapper-tutorial {
        padding: 48px 133px 48px 133px;
        box-shadow: 0 0 21px rgba(0, 138, 184, 0.13);
        border-radius: 10px;
        background-color: #ffffff;
        text-align: center;
        @include r($tablet) {
            padding: 30px 40px; }
        @include r($mobile) {
            padding: 15 5px; }
        h3 {
            color: #274386;
            font-size: 30px;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: 0.45px;
            margin-bottom: 18px;
            @include r($tablet) {
                font-size: 25px; }
            @include r($mobile) {
                font-size: 17px; } }
        .arrow-down {
            width: 50px;
            height: 35px;
            background: url(../img/arrow-down-2.png) no-repeat center;
            margin-bottom: 23px; }
        p {
            color: #d12835;
            font-size: 20px;
            font-weight: 700;
            letter-spacing: 0.1px;
            margin-bottom: 12px;
            @include r($mobile) {
                font-size: 19px; } }
        ol {
            list-style-position: inside; }
        li:last-child {
            margin-bottom: 0; }
        li {
            color: #000000;
            font-size: 16px;
            font-weight: 400;
            line-height: 22px;
            letter-spacing: 0.24px;
            font-weight: 400;
            margin-bottom: 10px;
            @include r($mobile) {
                font-size: 15px; }
            a[href^="http"] {
                color: #274386;
                text-decoration: underline; }
            span {
                font-weight: 700;
                a {
                    text-decoration: none;
                    color: #000000; } } } } }
