* {
  box-sizing: border-box;
  outline: none; }

body, html {
  background: white;
  color: $fontColor;
  font-family: $font-family;
  font-size: 16px;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  // overflow-x: hidden
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  &__content {
    // used for sticky footer (flexbox way)
    flex: 1 0 auto;
    display: flex;
    flex-direction: column; } }
// REMOVE FOR PRODUCTION !
.dev-bp-debug {
  background: black;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 9999;
  color: white;
  padding: 5px;
  font-size: 12px;
  border-bottom-left-radius: 5px; }
.container {
  width: 1180px;
  margin-left: auto;
  margin-right: auto;
  @include r($wide) {
    width: 970px; }
  @include r($desktop) {
    width: 740px; }
  @include r($tablet) {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px; } }
h1,h2,h3,h4,h5,h6,p,a,span,ul,ol,li {
  padding: 0;
  margin: 0; }
h2 {
  color: #274386;
  font-size: 50px;
  font-weight: 700;
  line-height: 61.02px;
  text-transform: uppercase;
  letter-spacing: 0.25px;
  span {
    color: #cf2c3a; } }
p {
  color: $fontColor;
  font-family: $font-family;
  font-size: 18px;
  font-weight: 300;
  line-height: 26px; }
.up_btn {
  position: fixed;
  right: 20px;
  bottom: 20px;
  cursor: pointer;
  width: 68px;
  height: 68px;
  z-index: 1;
  transition: 0.3s;
  background: url('../img/icon_up.png')no-repeat center;
  outline: none; }
.swiper-pagination-bullet {
  position: relative;
  width: 17px;
  height: 17px;
  border: 1px solid rgb(217, 217, 217);
  background: transparent;
  border-radius: 0;
  margin: 0 10px;
  opacity: 1;
  @include r($desktop) {
    margin-top: 40px; }
  &:after {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    width: 7px;
    height: 7px;
    transform: translate(-50%, -50%) scale(0);
    background-color: rgb(82, 184, 75);
    @include transition(transform); }
  &-active {
    border-color: rgb(82, 184, 75);
    &:after {
      transform: translate(-50%, -50%) scale(1); } } }
.input-text {
  padding-left: 16px;
  width: 100%;
  height: 51px;
  box-shadow: 0 0 7px rgba(209, 40, 53, 0.23);
  border-radius: 5px;
  border: 1px solid #cf2c3a;
  position: relative;
  transition: 0.3s;
  z-index: 2;
  background-color: transparent;
  &:hover {
    box-shadow: 0 0 21px rgba(209, 40, 53, 0.5); }
  &::-webkit-input-placeholder {
    color: #274386;
    font-family: 'Roboto';
    font-size: 15px;
    font-weight: 500;
    line-height: normal;
    letter-spacing: 0.21px;
    white-space: normal; }
  &::-moz-placeholder {
    color: #274386;
    font-family: 'Roboto';
    font-size: 15px;
    font-weight: 500;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.21px;
    white-space: normal; }
  &:-moz-placeholder {
    color: #274386;
    font-family: 'Roboto';
    font-size: 15px;
    font-weight: 500;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.21px;
    white-space: normal; }
  &:-ms-input-placeholder {
    color: #274386;
    font-family: 'Roboto';
    font-size: 15px;
    font-weight: 500;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.21px;
    white-space: normal; }
  &:focus::-webkit-input-placeholder {
    color: transparent; }
  &:focus::-moz-placeholder {
    color: transparent; }
  &:focus:-moz-placeholder {
    color: transparent; } }
.placeholder {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
  color: #274386;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0.21px;
  @include r($mobile) {
      left: 4px;
      letter-spacing: -0.21px;
      font-size: 13px; }
  &:focus:-ms-input-placeholder {
    color: transparent; } }
.swiper-button-next, .swiper-button-prev {
  top: auto;
  bottom: 0;
  background: none;
  color: transparent;
  width: 75px;
  height: 51px;
  border-radius: 10px;
  background-color: #a5a5a5;
  display: flex;
  justify-content: center;
  align-items: center;
  svg {
    width: 28px;
    height: 20px;
    fill: #fff; } }
.swiper-button-prev {
  left: calc( 50% - 48px );
  transform: translateX(-50%) translateY(50%);
  svg {
    transform: rotate(180deg); } }
.swiper-button-next {
  right: calc( 50% - 48px );
  transform: translateX(50%) translateY(50%); }
