// buttons
button {
  -webkit-apperance: none;
  border: 0;
  box-shadow: none;
  cursor: pointer;
  &:focus {
    outline: none; } }

.btn {
  height: 50px;
  box-shadow: 0 0 7px rgba(255, 255, 255, 0.35);
  border-radius: 5px;
  background-color: #c90312;
  border: none;
  color: #ffffff;
  font-size: 16px;
  font-weight: 700;
  line-height: 21.97px;
  letter-spacing: 0.84px;
  cursor: pointer;
  text-transform: uppercase;
  transition: 0.3s;
  &:hover {
    background-color: #86020c;
    box-shadow: 0 0 21px rgba(255, 255, 255, 0.5); } }
